.backdrop
{
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 100;
    height: 100vh;
    width: 100vw;
    position: fixed;
    transition: all 0.3s ease-out;
    display: flex;
    justify-content: center;
    align-items: center;
}